@import "./Variables.scss";

.project-container {
  width: 100%;
  background: #00031f;
  text-align: center;
  display: grid;
  grid-template-rows: 60px 1fr;
  place-items: center;
  min-height: 100vh;
  padding: 10px;
  padding-bottom: 50px;
}

.card-container {
  width: 100%;
  display: grid;
  // column-gap: 10px;
  // row-gap: 10px;
  gap: 10px;
  grid-template-rows: auto;
  grid-template-columns: repeat(3, 1fr);
}

.card {
  background: rgb(255, 255, 255);
  border-radius: 0.3rem;
  // box-shadow: 1px 1px 5px 1px rgb(194, 194, 194);
  width: 90%;
  max-width: 300px;
  min-width: 200px;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 1fr;
  gap: 20px;
  padding: 5px;
  justify-self: center;
}
.card-img {
  width: 100%;
  max-height: 300px;
  // max-width: 300px;
  aspect-ratio: 16 / 9;
  object-fit: cover;
  border-radius: 0.3rem;
}

.card-title {
  font-size: 16px;
  font-weight: bold;
}
.card-content {
  text-align: left;
  font-size: 14px;
}

.tech-container {
  background-color: $blue-color;
  border-radius: 2px;
  padding: 5px 0px 5px 0px;
  // border: $blue-color 1px solid;
}

.techstack {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  font-size: 24px;
  color: white;
  align-items: center;
}

@media (max-width: 500px) {
  .card-container {
    grid-template-columns: auto;
  }
  .card {
    grid-template-columns: auto;
    grid-template-rows: 1fr auto;
    font-size: 12px;
  }
  .card-img {
    max-height: 250px;
  }
}
